const { Card, CardHeader, CardBody, ToggleControl, TextControl, Button, SelectControl, Dashicon, FormTokenField, Dropdown, CheckboxControl, RangeControl, ResponsiveWrapper, ButtonGroup, TextareaControl } = wp.components
const { useState, useEffect } = wp.element;
const { escapeHTML } = wp.escapeHtml;
const { __ } = wp.i18n;
const { customize } = wp;
const { MediaUpload } = wp.blockEditor;
import {SortableContainer, SortableElement} from 'react-sortable-hoc';
import {arrayMoveImmutable} from 'array-move';
import Select from 'react-select'

import { __experimentalNumberControl as NumberControl } from '@wordpress/components';

// Block repeater control 
export const LocalNewsBlockRepeater = ( props ) =>  {
    const [ repeater, setRepeater ] = useState(JSON.parse(props.value))

    useEffect(() => {
        customize.value( props.setting )(JSON.stringify(repeater))
    }, [repeater]);

    function updateItemVisibility(itemKey) {
        let newRepeater = JSON.parse(JSON.stringify(repeater))
        newRepeater[itemKey]['option'] = newRepeater[itemKey]['option'] ? false : true
        setRepeater(JSON.parse(JSON.stringify(newRepeater)))
    }

    const onSortEnd = (e) => {
        var newRepeater = arrayMoveImmutable(repeater, e.oldIndex, e.newIndex )
        setRepeater(newRepeater)
    }

    function removeBlock(key) {
        if (key !== -1) repeater.splice(key, 1);
        setRepeater(JSON.parse(JSON.stringify(repeater)))
    }

    function updateValue(key, block) {
        repeater[key] = block
        setRepeater(JSON.parse(JSON.stringify(repeater)))
    }

    return (
        <>
            <label className="customize-control-title">{ customize.settings.controls[props.setting].label }</label>
            <span className="customize-control-description">{ customize.settings.controls[props.setting].description }</span>
            <BlockSortableList onSortEnd={onSortEnd} hideSortableGhost={false} repeater={repeater} removeBlock={removeBlock} updateValue={updateValue} props={props} updateItemVisibility={updateItemVisibility}/>
        </>
    )
}

const PostBlock = (props) => {
    const [ option, setOption ] = useState(props.props.option)
    const [ visible, setVisible ] = useState(props.props.option)
    const [ title, setTitle ] = useState(props.props.title)
    const [ dateOption, setDateOption ] = useState(props.props.dateOption)
    const [ authorOption, setAuthorOption ] = useState(props.props.authorOption)
    const [ categoryOption, setCategoryOption ] = useState(props.props.categoryOption)
    const [ commentOption, setCommentOption ] = useState(props.props.commentOption)
    const [ excerptOption, setExcerptOption ] = useState(props.props.excerptOption)
    const [ query, setQuery ] = useState(props.props.query)
    const [ buttonOption, setButtonOption ] = useState(props.props.buttonOption)
    const [ viewallOption, setViewallOption ] = useState(props.props.viewallOption)
    const [ viewallUrl, setViewallUrl ] = useState(props.props.viewallUrl)
    const [ column, setColumn ] = useState(props.props.column || 'two')
    const [ columns, setColumns ] = useState(props.props.columns || 4)
    const [ dots, setDots ] = useState(props.props.dots || true)
    const [ loop, setLoop ] = useState(props.props.loop || false)
    const [ arrows, setArrows ] = useState(props.props.arrows || true)
    const [ auto, setAuto ] = useState(props.props.auto || false)
    
    // update parent value
    useEffect(() => {
        let postBlockData = {
            'type': props.props.type,
            'option': option,
            'title': title,
            'dateOption': dateOption,
            'authorOption': authorOption,
            'categoryOption': categoryOption,
            'commentOption': commentOption,
            'excerptOption': excerptOption,
            'query': query,
            'buttonOption': buttonOption,
            'viewallOption': viewallOption,
            'viewallUrl': viewallUrl,
            'layout': 'one'
        }
        if( props.props.type == 'news-list' ) postBlockData.column = column
        if( props.props.type == 'news-carousel' ) {
            postBlockData.columns = columns
            postBlockData.dots = dots
            postBlockData.loop = loop
            postBlockData.arrows = arrows
            postBlockData.auto = auto
        }
        props.updateValue(props.itemKey, postBlockData)
    }, [option, title, dateOption, authorOption, categoryOption, commentOption, excerptOption, buttonOption, viewallOption, viewallUrl, column, columns, query, dots, loop, arrows, auto ])

    function removeBlock() {
        setVisible(false)
        props.removeBlock(props.itemKey)
    }
    return (
        <div className={ `block-item block--${props.props.option ? "visibility" : "hidden"}` } elevation={4} isRounded={false} size="x-small">
            <div className="block-header">
                <span className="control-title">{ escapeHTML( props.props.type.replace( '-', ' ' ) ) }</span>
                <Dropdown
                    position="bottom right"
                    renderToggle={ ( { isOpen, onToggle } ) => (
                        <span 
                        className="setting-icon dashicons dashicons-admin-generic"
                        onClick={ onToggle }
                        aria-expanded={ isOpen }></span>
                    ) }
                    renderContent={ () => <div className="block-content">
                                            <TextControl
                                                label={ __( escapeHTML( 'Block Title' ), 'localnews' ) }
                                                value={ title }
                                                onChange={ ( newValue ) => setTitle( newValue ) }
                                            />
                                            <ToggleControl
                                                label={ __( escapeHTML( 'Show post date' ), 'localnews' ) }
                                                checked={ dateOption }
                                                onChange={ (newToggle) => setDateOption( newToggle ) }
                                            />
                                            <ToggleControl
                                                label={ __( escapeHTML( 'Show post author' ), 'localnews' ) }
                                                checked={ authorOption }
                                                onChange={ (newToggle) => setAuthorOption( newToggle ) }
                                            />
                                            <ToggleControl
                                                label={ __( escapeHTML( 'Show post category' ), 'localnews' ) }
                                                checked={ categoryOption }
                                                onChange={ (newToggle) => setCategoryOption( newToggle ) }
                                            />
                                            <ToggleControl
                                                label={ __( escapeHTML( 'Show post comment' ), 'localnews' ) }
                                                checked={ commentOption }
                                                onChange={ (newToggle) => setCommentOption( newToggle ) }
                                            />
                                            { ( props.props.type != 'news-carousel' && props.props.type != 'news-filter' ) &&
                                                <>
                                                    <ToggleControl
                                                        label={ __( escapeHTML( 'Show read more button' ), 'localnews' ) }
                                                        checked={ buttonOption }
                                                        onChange={ (newToggle) => setButtonOption( newToggle ) }
                                                    />
                                                    <ToggleControl
                                                        label={ __( escapeHTML( 'Show post excerpt' ), 'localnews' ) }
                                                        checked={ excerptOption }
                                                        onChange={ (newToggle) => setExcerptOption( newToggle ) }
                                                    />
                                                </>
                                            }
                                            <PostQuery query={props.props.query} setParentQuery={setQuery}/>
                                            { props.props.type == 'news-list' &&
                                                <SelectControl
                                                    label={ __( escapeHTML( 'No. of columns' ), 'localnews' ) }
                                                    value={ column }
                                                    options={ [
                                                        { label: __( escapeHTML( '1' ), 'localnews' ), value: 'one' },
                                                        { label: __( escapeHTML( '2' ), 'localnews' ), value: 'two' },
                                                        { label: __( escapeHTML( '3' ), 'localnews' ), value: 'three' }
                                                    ] }
                                                    onChange={ ( newColumn ) => setColumn( newColumn ) }
                                                />
                                            }
                                            <ToggleControl
                                                label={ __( escapeHTML( 'Show view all icon' ), 'localnews' ) }
                                                checked={ viewallOption }
                                                onChange={ (newToggle) => setViewallOption( newToggle ) }
                                            />
                                            { viewallOption &&
                                                <TextControl
                                                    label={ __( escapeHTML( 'View all URL' ), 'localnews' ) }
                                                    value={ viewallUrl }
                                                    onChange={ ( newUrl ) => setViewallUrl( newUrl ) }
                                                />
                                            }
                                            { props.props.type == 'news-carousel' &&
                                                <>
                                                    <RangeControl
                                                        label={ __( escapeHTML( 'No. of columns' ), 'localnews' ) }
                                                        value={ columns }
                                                        allowReset = {true}
                                                        onChange={ ( newColumns ) => setColumns( newColumns ) }
                                                        min={ 2 }
                                                        max={ 4 }
                                                        step={ 1 }
                                                        resetFallbackValue={4}
                                                    />
                                                    <CheckboxControl
                                                        label= { __( escapeHTML( 'Show dots / pagers' ), 'localnews' ) }
                                                        checked={ dots }
                                                        onChange={ (newDots) => setDots(newDots) }
                                                    />
                                                    <CheckboxControl
                                                        label= { __( escapeHTML( 'Show controller / arrows' ), 'localnews' ) }
                                                        checked={ arrows }
                                                        onChange={ (newArrows) => setArrows(newArrows) }
                                                    />
                                                    <CheckboxControl
                                                        label= { __( escapeHTML( 'Enable loop items' ), 'localnews' ) }
                                                        checked={ loop }
                                                        onChange={ (newLoop) => setLoop(newLoop) }
                                                    />
                                                    <CheckboxControl
                                                        label= { __( escapeHTML( 'Enable auto mode' ), 'localnews' ) }
                                                        checked={ auto }
                                                        onChange={ (newAuto) => setAuto(newAuto) }
                                                    />
                                                </>
                                            }
                                        </div>
                    }
                />
                <span className={`display-icon dashicons dashicons-${props.props.option ? "visibility" : "hidden"}`} onClick={() => props.updateItemVisibility(props.itemKey)}></span>
                {  ( props.itemKey > 100 )&&
                    <span className="remove-icon"><Dashicon icon="minus" data-toggle="tooltip" title={ __( escapeHTML( 'Remove item' ), 'localnews' ) } onClick={() => removeBlock()}/></span>
                }
            </div>
        </div>
    )
}

const PostAdBlock = (props) => {
    const [ option, setOption ] = useState(props.props.option)
    const [ visible, setVisible ] = useState(props.props.option)
    const [ title, setTitle ] = useState(props.props.title)
    const [ media, setMedia ] = useState(props.props.media)
    const [ url, setUrl ] = useState(props.props.url)
    const [ targetAttr, setTargetAttr ] = useState(props.props.targetAttr)
    const [ relAttr, setRelAttr ] = useState(props.props.relAttr)

    // update parent value
    useEffect(() => {
        let postBlockData = {
            'type': props.props.type,
            'option': option,
            'title': title,
            'media': media,
            'url': url,
            'targetAttr': targetAttr,
            'relAttr': relAttr
        }
        props.updateValue(props.itemKey, postBlockData)
    }, [option, title, media, url, targetAttr, relAttr ])

    function removeBlock() {
        setVisible(false)
        props.removeBlock(props.itemKey)
    }

    const removeMedia = () => {
		setMedia({
			media_id: 0,
			media_url: ''
		});
	}

    const onSelectMedia = (newMedia) => {
		setMedia({
			media_id: newMedia.id,
			media_url: newMedia.url
		});
	}

    return (
        <div className={ `block-item block--${props.props.option ? "visibility" : "hidden"}` } elevation={4} isRounded={false} size="x-small">
            <div className="block-header">
                <span className="control-title">{ escapeHTML( props.props.type.replace( '-', ' ' ) ) }</span>
                <Dropdown
                        position="bottom right"
                        renderToggle={ ( { isOpen, onToggle } ) => (
                            <span 
                            className="setting-icon dashicons dashicons-admin-generic" 
                            onClick={ onToggle }
                            aria-expanded={ isOpen }></span>
                        ) }
                        renderContent={ () => 
                            <div className="block-content">
                                <TextControl
                                    label={ __( escapeHTML( 'Block Title' ), 'localnews' ) }
                                    value={ title }
                                    onChange={ ( newValue ) => setTitle( newValue ) }
                                />
                                <div className="media-field">
                                    <MediaUpload
                                        onSelect={onSelectMedia}
                                        value={media.media_id}
                                        allowedTypes={ ['image'] }
                                        render={({open}) => (
                                            <Button 
                                                className={media.media_id == 0 ? 'media-field-image__toggle' : 'media-field-image__preview'}
                                                onClick={open}
                                            >
                                                {media.media_id == 0 && __('Upload an image', 'localnews')}
                                                {media != undefined && 
                                                    <ResponsiveWrapper
                                                        naturalWidth={ 200 }
                                                        naturalHeight={ 200 }
                                                    >
                                                        <img src={media.media_url} />
                                                    </ResponsiveWrapper>
                                                    }
                                            </Button>
                                        )}
                                    />
                                    {media.media_id != 0 && 
                                        <MediaUpload
                                            title={__('Replace image', 'localnews')}
                                            value={media.media_id}
                                            onSelect={onSelectMedia}
                                            allowedTypes={['image']}
                                            render={({open}) => (
                                                <Button onClick={open} variant="secondary" isLink isLarge>{__('Replace image', 'localnews')}</Button>
                                            )}
                                        />
                                    }
                                    {media.media_id != 0 && 
                                        <Button onClick={removeMedia} isLink isDestructive>{__('Remove image', 'localnews')}</Button>
                                    }
                                </div>
                                <TextControl
                                    label={ __( escapeHTML( 'URL' ), 'localnews' ) }
                                    value={ url }
                                    onChange={ ( newUrl ) => setUrl( newUrl ) }
                                />
                                <SelectControl
                                    label={ __( escapeHTML( 'Ad link open in' ), 'localnews' ) }
                                    value={ targetAttr }
                                    options={ [
                                        { label: __( escapeHTML( 'Open link in new tab' ), 'localnews' ), value: '_blank' },
                                        { label: __( escapeHTML( 'Open link in same tab' ), 'localnews' ), value: '_self' }
                                    ] }
                                    onChange={ ( newTargetAttr ) => setTargetAttr( newTargetAttr ) }
                                />
                                <SelectControl
                                    label={ __( escapeHTML( 'Link rel attribute value' ), 'localnews' ) }
                                    value={ relAttr }
                                    options={ [
                                        { label: __( escapeHTML( 'nofollow' ), 'localnews' ), value: 'nofollow' },
                                        { label: __( escapeHTML( 'noopener' ), 'localnews' ), value: 'noopener' },
                                        { label: __( escapeHTML( 'noreferrer' ), 'localnews' ), value: 'noreferrer' }
                                    ] }
                                    onChange={ ( newRelAttr ) => setRelAttr( newRelAttr ) }
                                />
                            </div>
                        }
                    />
                <span className={`display-icon dashicons dashicons-${props.props.option ? "visibility" : "hidden"}`} onClick={() => props.updateItemVisibility(props.itemKey)}></span>
                {  ( props.itemKey > 100 )&&
                    <span className="remove-icon"><Dashicon icon="minus" data-toggle="tooltip" title={ __( escapeHTML( 'Remove item' ), 'localnews' ) } onClick={() => removeBlock()}/></span>
                }
            </div>
        </div>
    )
}

const PostQuery = (props) => {
    const [ order, setOrder ] = useState(props.query.order);
    const [ count, setCount ] = useState(props.query.count);
    const [ categories, setCategories ] = useState(props.query.categories);
    const [ ids, setIds ] = useState(props.query.ids);
    const choices = customizerControlsObject.categories
    
    useEffect(() => {
        props.setParentQuery({ order: order, count: count, categories: categories, ids: ids })
    }, [order, count, ids, categories])

    return (
        <div className="post-query-field">
            <label className="post-query-field-label">{ __( escapeHTML( 'Posts query' ), 'localnews' ) }</label>
            <div>
                <Dropdown
                    position="bottom right"
                    renderToggle={ ( { isOpen, onToggle } ) => (
                        <Dashicon
                            className="popover-trigger"
                            icon="edit" 
                            onClick={ onToggle }
                            aria-expanded={ isOpen }
                        />
                    )}
                    renderContent= { () =>
                        <Card>
                            <CardBody>
                                <SelectControl
                                    label={ __( escapeHTML( 'Orderby' ), 'localnews' ) }
                                    value={ order }
                                    options={ [
                                        { label: __( escapeHTML( 'Newest - Oldest' ), 'localnews' ), value: 'date-desc' },
                                        { label: __( escapeHTML( 'Oldest - Newest' ), 'localnews' ), value: 'date-asc' },
                                        { label: __( escapeHTML( 'A - Z' ), 'localnews' ), value: 'title-asc' },
                                        { label: __( escapeHTML( 'Z - A' ), 'localnews' ), value: 'title-desc' }
                                    ] }
                                    onChange={ ( newOrder ) => setOrder( newOrder ) }
                                />
                                <NumberControl
                                    label = { __( escapeHTML( 'Number of posts to display' ), 'localnews' ) }
                                    isShiftStepEnabled={ true }
                                    onChange={ (newNumber) => setCount( newNumber ) }
                                    shiftStep={ 5 }
                                    value={ count }
                                />
                                <FormTokenField
                                    label= { __( escapeHTML( 'Exclude post ids' ), 'localnews' ) }
                                    value= { ids }
                                    onChange={ ( newids ) => setIds( newids ) }
                                />
                                { ( choices && categories ) && 
                                    <Select
                                        isMulti={true}
                                        inputId="local-news-search-in-select"
                                        isSearchable ={true}
                                        heading={__( escapeHTML( 'Post categories' ), 'localnews' )}
                                        value={categories}
                                        options={choices}
                                        onChange={ ( newCategories ) => setCategories( newCategories ) }
                                    />
                                }
                            </CardBody>
                        </Card>
                    }
                />
            </div>
        </div>
    )
}

const blockSortList = ({repeater, removeBlock, updateValue, props, updateItemVisibility}) => {
    return (
        <div className="blocks-wrap">
            { repeater &&
                repeater.map((block,index) => {
                    return(
                        <BlockSortableItem
                            item={block}
                            key={key}
                            index={index}
                            disabled={!block.option}
                            itemKey={index}
                            props={block}
                            removeBlock={removeBlock}
                            updateValue={updateValue}
                            setting={props.setting}
                            updateItemVisibility={updateItemVisibility}
                        />
                    )
                })
            }
        </div>
    );
};

const blockSortItem = ({props,itemKey,removeBlock,updateValue,setting, updateItemVisibility}) => {
    switch( props.type ) {
        case "ad-block": return (<PostAdBlock props={props} itemKey={itemKey} removeBlock={removeBlock} updateValue={updateValue} setting={setting} updateItemVisibility={updateItemVisibility}/>)
            break;
        default: return (<PostBlock props={props} itemKey={itemKey} removeBlock={removeBlock} updateValue={updateValue} setting={setting} updateItemVisibility={updateItemVisibility}/>)
    }
}

const BlockSortableList = SortableContainer(blockSortList);
const BlockSortableItem = SortableElement(blockSortItem)